/**
 * ForgetPasswordContainer.js
 * Used when users are resetting password.
 * The link will be given in the email.
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import ForgetPassword from '../../components/ForgetPassword/ForgetPassword';
import PopupWithText from '../../components/Templates/PopupWithText/PopupWithText';

import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { resetPassword } from '../../actions/userActions';

class ForgetPasswordContainer extends Component {
  constructor(props) {
    super(props);

    const { location } = this.props;
    let token = null;
    try {
      token = location.search.substring(_.findIndex(location.search, char => char === '=') + 1);
    } catch (error) {
      console.log('Token parse error');
    }

    this.state = {
      token,
    };

    this.submitCallback = this.submitCallback.bind(this);
  }

  async submitCallback(password) {
    const { token } = this.state;
    const { loading, loadingEnd } = this.props;

    loading();
    const result = await resetPassword(password, token);
    loadingEnd();

    const message = result ? 'Your password has been reset successfully.' : 'Password reset failed. Please try again.';
    setTimeout(() => this.popup.openPopupWithText(message, result), 300);
  }

  render() {
    const { token } = this.state;
    const { history } = this.props;

    if (!token) {
      return null;
    }

    return (
      <Fragment>
        <ForgetPassword
          submitCallback={this.submitCallback}
        />
        <PopupWithText
          ref={node => this.popup = node}
          onConfirm={() => history.push('/')}
        />
      </Fragment>
    );
  }
}
ForgetPasswordContainer.propTypes = {
  location: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  loginControl: state.login.loginControl,
});

const mapDispatchToProps = dispatch => ({
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ForgetPasswordContainer));
