import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import TextInput from '../Forms/TextInput/TextInput';
import Button from '../FormsV3/Button/Button';

import './ForgetPassword.scss';

class ForgetPassword extends Component {
  constructor(props) {
    super(props);

    this.state = {
      errorString: null,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(e) {
    const { submitCallback } = this.props;
    if (!e.password1 || !e.password2) {
      this.setState({
        errorString: 'Please fill in both password fields.',
      });

      return;
    }

    if (e.password1 !== e.password2) {
      this.setState({
        errorString: 'Password are not the same.',
      });

      return;
    }

    if (e.password1.length < 8) {
      this.setState({
        errorString: 'Password must be at least 8 characters.',
      });
      return;
    }

    this.setState({
      errorString: null,
    });

    submitCallback(e.password1);
  }

  render() {
    const { errorString } = this.state;
    const { handleSubmit, form, change } = this.props;
    return (
      <Row className="reset-password">
        <Col className="reset-text" xs={12}>
          <p>Reset Password</p>
        </Col>
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="password1"
              placeholderString="Enter your new Password"
              size="medium"
              isPassword
              desc="New Password"
              zIndex={99}
            />
          </Col>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="password2"
              placeholderString="Confirm Password"
              size="medium"
              isPassword
              desc="Confirm Password"
              zIndex={98}
            />
          </Col>
          <Col xs={12}>
            <p className="error-text">{errorString}</p>
          </Col>
          <Col className="justify-content-center button" xs={12}>
            <Button type="submit" variant="medium-color" width={165} height={72}>
              <p>Reset Password</p>
            </Button>
          </Col>
        </form>
      </Row>
    );
  }
}

ForgetPassword.defaultProps = {
};

ForgetPassword.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  submitCallback: PropTypes.func.isRequired,
};

export default reduxForm({
  form: 'reset-password',
})(ForgetPassword);

